// 52p
import React from 'react';
import Todo from './Todo';
import AddTodo from './AddTodo';
import { Paper, List, Container, Grid, Button, AppBar, Toolbar, Typography, GridList } from '@material-ui/core';
import './App.css';
import { call,signout } from './service/ApiService';

class App extends React.Component{
    constructor(props){ // props 
      super(props); // props 
      this.state = { // item item.id, item.title, item.done    
        items: [],
        /*          */
        loading:true,

      };
    }
    //add  
    add = (item) => {
        call("/todo","POST",item).then((response) => 
            this.setState({items:response.data})
        );
    }

    update = (item) => {
        call("/todo","DELETE",item).then((response) =>
            this.setState({item:response.data})
        );
    }
    
    // componentDidmount ()     .
    componentDidMount(){
        call("/todo","GET",null).then((response) =>
            this.setState({item:response.data, loading:false })
        );
    }
    
    render(){
        //todoItems this.state.items.length 0  true &&  .
        //todoItem = this.state.items.length > 0 ? (<paper></paper>):"";   .  ? ternary operator
        var todoItems = this.state.items.length > 0 && (
            <Paper style={{margine:16}}>
                <List>
                { this.state.items.map((item,idx) => (
                    <Todo item={item} key={item.id} delete={this.delete} update={this.update} />
                ))}
                </List>
            </Paper>

        );

        //navigationBar
        var navigationBar = (
            <AppBar position="static">
                <Toolbar>
                    <Grid justifyContent="space-between" container>
                        <Grid item>
                            <Typography variant="h6"> </Typography>
                        </Grid>
                        <Grid item>
                            <Button color="inherit" onClick={signout}>logout
                            </Button>
                        </Grid>

                    </Grid>
                </Toolbar>
            </AppBar>
        );

        //loading   
        var todoListPage = (
            <div>
            {navigationBar}
            <Container maxWidth="md">
                <AddTodo add={this.add} />
                <div className="TodoList">{todoItems}</div>
            </Container>
            </div>
        );

        //loading  
        var loadingPage = <h1>..</h1>
        var content = loadingPage;

        if(!this.state.loading) {
            content = todoListPage;
        }
        //  JPX .
        return (
            <div className="App">
                {content}
            </div>
        );
    }
}

export default App;